/**
* 法规库-裁量基准组件
* @module 案件
* @desc 历史版本
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <el-dialog
            :close-on-click-modal="false"
            :visible="show"
            @close="closeDialog"
            center
            title="全部版本"
            width="900px"
    >
        <!-- 表格 -->
        <el-table
                :data="list"
                :header-cell-style="{background:'#e9eff8'}"
                @row-click="showFunction"
                border
                size="mini"
                v-loading="loading"
        >
            <el-table-column align="center" label="基准名称" prop="dsName"/>
            <el-table-column
                    :show-overflow-tooltip="true"
                    align="center"
                    label="依据条例"
                    prop="accordingRegulations"
            />
            <el-table-column
                    :show-overflow-tooltip="true"
                    align="center"
                    label="适用情形"
                    prop="suitsituation"
            />
            <el-table-column
                    :show-overflow-tooltip="true"
                    align="center"
                    label="罚款幅度"
                    prop="pubnishextend"
            />
            <el-table-column align="center" label="是否生效" prop="isEffective" width="100">
                <template slot-scope="scope">{{scope.row.isEffective|isEffective}}</template>
            </el-table-column>
            <el-table-column align="center" label="操作" width="80">
                <template slot-scope="scope">
                    <el-tooltip :open-delay="500" content="修改当前项" placement="top">
                        <el-button
                                @click.stop="editFunction(scope.$index, scope.row)"
                                plain
                                size="mini"
                                type="warning"
                                v-if="scope.row.isEffective == 2"
                        >编辑
                        </el-button>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <i-pagination
                :pageNum.sync="form.pageNum"
                :pageSize.sync="form.pageSize"
                :total="total"
                @getData="getList"
                class="page-css"
        />
        <!-- 编辑&添加弹框 -->
        <change-edit
                :isRevise="editObj.isRevise"
                :onlyShow="editObj.onlyShow"
                :see="see"
                :show.sync="editObj.show"
                :title="editObj.title"
                :type="editObj.editId"
                @getList="getList"
        />
        <!-- 查看 -->
        <see-content :editId="seeObj.editId" :show.sync="seeObj.show" :title="seeObj.title"></see-content>
    </el-dialog>
</template>

<script>
    import iPagination from "../../../../components/LjPagination";
    import searchForm from "../../../../components/LjSearchForm";
    import {formatDate, isEffective} from "../../../../until/wordBook";
    import changeEdit from "./editadd";
    import seeContent from "./seeContent";

    import {apiDsSearch, apiPunishMatterDelete} from "../../../../api/case";

    export default {
        props: ["show", "id"],
        data() {
            return {
                loading:true,
                //弹出框数据
                editObj: {
                    show: false,
                    title: "",
                    editId: null
                },
                seeObj: {
                    show: false,
                    title: "",
                    editId: null
                },
                //搜索组件数据
                searchObj: null,
                form: {
                    pageSize: 20,
                    pageNum: 1
                },
                total: 0,
                list: [],
                see: false
            };
        },
        components: {
            changeEdit,
            iPagination,
            searchForm,
            seeContent
        },

        methods: {
            //关闭弹框
            closeDialog() {
                this.$emit("update:show", false);
            },

            //查看
            showFunction(row) {
                this.seeObj.show = true;
                this.seeObj.title = "查看全部";
                this.seeObj.editId = row.cdsId;
            },

            //查询+搜索
            getList() {
                apiDsSearch(this.form).then(res => {
                    this.loading=false;
                    this.total = res.total;
                    this.list = res.data;
                    this.$emit("getList");
                });
            },

            //编辑
            editFunction(index, row) {
                this.editObj.editId = row.cdsId;
                this.editObj.title = "修改";
                this.editObj.isRevise = false;
                this.editObj.onlyShow = false;
                this.editObj.show = true;
                this.see = false;
            },
            initPage() {
                this.form.dsId = this.id;
                this.getList();
            },

            //编辑
            reviseFunction(index, row) {
                this.editObj.editId = row.tid;
                this.editObj.title = "修订";
                this.editObj.show = true;
                this.editObj.isRevise = true;
            },

            //删除按钮事件
            deleteFunction(index, data) {
                this.$confirm("此操作将永久删除该领域, 是否继续?", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                }).then(() => {
                    this.loading=true;
                    apiPunishMatterDelete(data.tid).then(res => {
                        this.loading=false;
                        this.getList();
                        this.$message({
                            type: "success",
                            message: "删除成功!"
                        });
                    });
                });
            }
        },
        filters: {
            formatDate(val) {
                return formatDate(val, "yyyy-MM-dd");
            },
            isEffective(val) {
                return isEffective[val];
            }
        },
        watch: {
            show(value) {
                if (value) {
                    this.initPage();
                }
            }
        }
    };
</script>

<style scoped>
    .page-css {
        margin-top: 10px;
    }
</style>
