/**
* 法规库-裁量基准组件
* @module 案件
* @desc 查看
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <el-dialog
            :close-on-click-modal="false"
            :title="title"
            :visible="show"
            @close="closeDialog"
            append-to-body
            width="750px"
           
    >
        <div  v-loading="loading">
            <h3 class="h3-css">{{form.dsName}}</h3>
            <el-divider content-position="left">
                <span class="divider-span">启用：{{form.isEffective | YESORNO}}</span>
            </el-divider>
            <el-form :model="form" label-width="140px" ref="ruleForm" size="medium">
                <el-row :gutter="20">
                    <!-- <el-col :span="12">
                                  <el-form-item label="裁量基准名称：" prop="dsName">
                                      <span class="fontcss"></span>
                                  </el-form-item>
                    </el-col>-->
                    <el-col :span="12">
                        <el-form-item label="执法领域：">
                            <span class="fontcss">{{form.enforceName}}</span>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="法律法规：">
                            <span class="fontcss">{{form.lawName}}</span>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="处罚事项：">
                            <span class="fontcss">{{form.pmName}}</span>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="依据条例：">
                            <span class="fontcss">{{form.accordingRegulations}}</span>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="依据条例内容：">
                            <span class="fontcss">{{form.accordingRegulationsContent}}</span>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="适用情形：">
                            <span class="fontcss">{{form.suitsituation}}</span>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="罚款幅度：">
                            <span class="fontcss">{{form.pubnishextend}}</span>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="自然人罚款范围：">
                            <span class="fontcss">{{form.naturalRangeMin}}-{{form.naturalRangeMax}}元</span>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="法人罚款范围：">
                            <span class="fontcss">{{form.legalRangeMin}}-{{form.legalRangeMax}}元</span>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="备注：">
                            <span class="fontcss">{{form.remark}}</span>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>

            <el-divider content-position="right">
        <span class="divider-span" v-if="form.effectiveTime">
          生效时间：{{ form.effectiveTime | formatDate}}
          <span v-if="form.invalidTime">至 {{ form.invalidTime | formatDate}}</span>
        </span>
            </el-divider>
        </div>
    </el-dialog>
</template>

<script>
    import {formatDate, YESORNO} from "../../../../until/wordBook";
    import {apiDsGet} from "../../../../api/case";

    export default {
        name: "seeContent",
        props: {
            editId: {
                type: String,
                default: ""
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: ""
            }
        },
        data() {
            let checkDate = (rule, value, callback) => {
                if (!value && this.form.isEffective == 2) {
                    return callback(new Error("请选择生效日期"));
                } else {
                    callback();
                }
            };
            return {
                loading:true,
                onlyShow: false,
                see: false,
                form: {},
                enforceList: [],
                lawList: [],
                pmList: [],
                rules: {
                    dsName: [
                        {required: true, message: "请输入裁量基准名称", trigger: "blur"}
                    ],
                    enforceId: [
                        {required: true, message: "请选择执法领域", trigger: "blur"}
                    ],
                    lawId: [{required: true, message: "请选择法律法规", trigger: "blur"}],
                    pmId: [{required: true, message: "请选择处罚事项", trigger: "blur"}],
                    accordingRegulations: [
                        {required: true, message: "请输入依据条例", trigger: "blur"}
                    ],
                    accordingRegulationsContent: [
                        {required: true, message: "请输入依据条例内容", trigger: "blur"}
                    ],
                    suitsituation: [
                        {required: true, message: "请填写适用情形", trigger: "blur"}
                    ],
                    pubnishextend: [
                        {required: true, message: "请输入职权依据", trigger: "blur"}
                    ],
                    naturalRangeMin: [
                        {required: true, message: "请输入罚款下限", trigger: "blur"}
                    ],
                    naturalRangeMax: [
                        {required: true, message: "请输入罚款上限", trigger: "blur"}
                    ],
                    legalRangeMin: [
                        {required: true, message: "请输入罚款下限", trigger: "blur"}
                    ],
                    legalRangeMax: [
                        {required: true, message: "请输入罚款上限", trigger: "blur"}
                    ],
                    effectiveTime: [{validator: checkDate, trigger: "blur"}]
                }
            };
        },
        methods: {
            //关闭弹框
            closeDialog() {
                this.$emit("update:show", false);
            },

            //加载弹框
            initPage() {
                apiDsGet(this.editId).then(res => {
                    this.loading=false;
                    this.form = res.data;
                });
            }
        },
        filters: {
            //是否
            YESORNO(val) {
                return YESORNO[val];
            },
            //时间戳
            formatDate(val) {
                return formatDate(Number(val), "yyyy-MM-dd");
            }
        },
        //监听，如果show为true,则执行initPage()
        watch: {
            show(value) {
                if (value) {
                    this.initPage();
                }
            }
        }
    };
</script>

<style lang="less" scoped>
    .el-form-item {
        margin-bottom: 0;
    }

    .divider-span {
        color: #cccccc;
    }

    .concss {
        font-weight: bold;
    }

    .h3-css {
        text-align: center;
    }
</style>
