/**
* 法规库-裁量基准组件
* @module 案件
* @desc 修改添加
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog
                :close-on-click-modal="false"
                :title="title"
                :visible="show"
                @close="closeDialog"
                append-to-body
                center
                width="800px"
        >
            <el-form :model="form" :rules="rules" label-width="140px" ref="ruleForm" size="medium" v-loading="loading">
                <el-row :gutter="20">
                    <el-col :span="12">
                        <el-form-item label="执法领域：">{{form.enforceName}}</el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="法律法规：">{{form.lawName}}</el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="处罚事项：">{{form.pmName}}</el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="裁量基准名称：" prop="dsName">
                            <el-input :readonly="onlyShow" placeholder="请输入裁量基准名称" v-model="form.dsName"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="依据条例：" prop="accordingRegulations">
                            <el-input
                                    :readonly="onlyShow"
                                    placeholder="请输入依据条例"
                                    v-model="form.accordingRegulations"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="依据条例内容：" prop="accordingRegulationsContent">
                            <el-input
                                    :autosize="{minRows:4}"
                                    :readonly="onlyShow"
                                    placeholder="请输入依据条例内容"
                                    type="textarea"
                                    v-model="form.accordingRegulationsContent"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="适用情形：" prop="suitsituation">
                            <el-input :readonly="onlyShow" placeholder="请输入适用情形"
                                      v-model="form.suitsituation"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="罚款幅度：" prop="pubnishextend">
                            <el-input :readonly="onlyShow" placeholder="请输入罚款幅度"
                                      v-model="form.pubnishextend"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="自然人罚款下限：" prop="enforceCode">
                            <el-input-number
                                    :disabled="onlyShow"
                                    :min="0"
                                    :step="50"
                                    placeholder="自然人罚款下限"
                                    v-model="form.naturalRangeMin"
                            ></el-input-number>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="自然人罚款上限：" prop="enforceCode">
                            <el-input-number
                                    :disabled="onlyShow"
                                    :min="form.naturalRangeMin"
                                    :step="50"
                                    placeholder="自然人罚款上限"
                                    v-model="form.naturalRangeMax"
                            ></el-input-number>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="法人罚款下限：" prop="enforceCode">
                            <el-input-number
                                    :disabled="onlyShow"
                                    :min="0"
                                    :step="50"
                                    placeholder="法人罚款下限"
                                    v-model="form.legalRangeMin"
                            ></el-input-number>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="法人罚款上限：" prop="enforceCode">
                            <el-input-number
                                    :disabled="onlyShow"
                                    :min="form.legalRangeMin"
                                    :step="50"
                                    placeholder="法人罚款上限"
                                    v-model="form.legalRangeMax"
                            ></el-input-number>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="备注：">
                            <el-input
                                    :readonly="onlyShow"
                                    placeholder="请输入备注"
                                    type="textarea"
                                    v-model="form.remark"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="是否立即生效：" v-if="type">
                            <el-switch
                                    :active-value="1"
                                    :disabled="onlyShow"
                                    :inactive-value="2"
                                    v-model="form.isEffective"
                            ></el-switch>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12" v-if="form.isEffective == 2">
                        <el-form-item label="生效时间：">
                            <el-date-picker
                                    :readonly="onlyShow"
                                    placeholder="选择日期"
                                    type="date"
                                    v-model="form.effectiveTime"
                                    value-format="timestamp"
                            ></el-date-picker>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <span class="dialog-footer" slot="footer" v-if="!onlyShow">
        <el-button @click="closeDialog" size="small" type="info">
          <i class="iconfont icon-back"></i>取 消
        </el-button>
        <el-button @click="submitForm('ruleForm')" size="small" type="primary">
          <i class="iconfont icon-save"></i>确 定
        </el-button>
      </span>
        </el-dialog>
    </div>
</template>
<script>
    import {apiDsAdd, apiDsGet, apiDsRevise, apiDsUpdate, apiPunishMatterGet} from "../../../../api/case";
    import {formatDate} from "../../../../until/wordBook";

    export default {
        name: "edit",
        props: {
            show: {
                type: Boolean,
                default: false
            },
            pmId: {
                type: String,
                default: ""
            },
            tid:{
                type: String,
                default: ""
            },
            onlyShow: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: "编辑"
            },
            type: {
                type: String,
                default: ""
            },
            isRevise: {
                type: Boolean,
                default: false
            },
            see: {
                type: Boolean,
                default: false
            }
        },
        data() {
            let checkDate = (rule, value, callback) => {
                if (!value && this.form.isEffective == 2) {
                    return callback(new Error("请选择生效日期"));
                } else {
                    callback();
                }
            };
            return {
                loading:true,
                form: {},
                enforceList: [],
                lawList: [],
                pmList: [],
                rules: {
                    dsName: [
                        {required: true, message: "请输入裁量基准名称", trigger: "blur"}
                    ],
                    accordingRegulations: [
                        {required: true, message: "请输入依据条例", trigger: "blur"}
                    ],
                    accordingRegulationsContent: [
                        {required: true, message: "请输入依据条例内容", trigger: "blur"}
                    ],
                    suitsituation: [
                        {required: true, message: "请填写适用情形", trigger: "blur"}
                    ],
                    pubnishextend: [
                        {required: true, message: "请输入职权依据", trigger: "blur"}
                    ],
                    naturalRangeMin: [
                        {required: true, message: "请输入罚款下限", trigger: "blur"}
                    ],
                    naturalRangeMax: [
                        {required: true, message: "请输入罚款上限", trigger: "blur"}
                    ],
                    legalRangeMin: [
                        {required: true, message: "请输入罚款下限", trigger: "blur"}
                    ],
                    legalRangeMax: [
                        {required: true, message: "请输入罚款上限", trigger: "blur"}
                    ],
                    effectiveTime: [{validator: checkDate, trigger: "blur"}]
                }
            };
        },
        methods: {
            //关闭弹出
            closeDialog() {
                this.$emit("update:show", false);
            },

            //提交
            submitForm() {
                this.$refs.ruleForm.validate(valid => {
                    if (valid) {
                        this.loading=true;
                        if (this.type) {
                            //编辑
                            if (this.isRevise) {
                                apiDsRevise(this.form).then(res => {
                                    this.loading=false;
                                    this.closeDialog();
                                    this.$emit("getList");
                                    this.$message({
                                        type: "success",
                                        message: "修改成功!"
                                    });
                                });
                            } else {
                                apiDsUpdate(this.form).then(res => {
                                    this.loading=false;
                                    this.closeDialog();
                                    this.$emit("getList");
                                    this.$message({
                                        type: "success",
                                        message: "修改成功!"
                                    });
                                });
                            }
                        } else {
                            //添加
                            apiDsAdd(this.form).then(res => {
                                this.loading=false;
                                this.$emit("getList");
                                this.closeDialog();
                                this.$message({
                                    type: "success",
                                    message: "添加成功!"
                                });
                            });
                        }
                    }
                });
            },

            //弹出时加载
            initPage() {
                if (this.type) {
                    //修改
                    apiDsGet(this.type).then(res => {
                        this.loading=false;
                        this.form = res.data;
                    });
                } else {
                    //添加
                    apiPunishMatterGet(this.tid).then(res => {
                        this.loading=false;
                        this.form = {
                            enforceName: res.data.enforceName,
                            lawName: res.data.lawName,
                            pmName: res.data.pmName,
                            pmId: res.data.pmId,
                            enforceId: res.data.enforceId,
                            lawId: res.data.lawId,
                            dsName: "",
                            accordingRegulations: res.data.accordingRegulations,
                            accordingRegulationsContent: res.data.accordingRegulationsContent,
                            suitsituation: "",
                            pubnishextend: "",
                            naturalRangeMin: res.data.naturalRangeMin,
                            naturalRangeMax: res.data.naturalRangeMax,
                            legalRangeMin: res.data.legalRangeMin,
                            legalRangeMax: res.data.legalRangeMax,
                            pmVersion: res.data.version,
                            lawVersion: res.data.lawVersion,
                            remark: "",
                            isEffective: 1,
                            effectiveTime: ""
                        };
                    });
                }
            }
        },
        filters: {
            //时间戳
            formatDate(value) {
                return formatDate(value, "yyyy-MM-dd hh:mm:ss");
            }
        },
        //监听，如果show为true,则执行initPage()
        watch: {
            show(value) {
                if (value) {
                    this.initPage();
                    //清除表单验证
                    this.$nextTick(() => {
                        this.$refs.ruleForm.clearValidate();
                    });
                }
            }
        }
    };
</script>
<style lang='less' scoped>
    .alert_wrop {
        padding: 10px;
        display: flex;
        color: red;
        justify-content: space-between;
    }

    .fontcss {
        font-family: "Microsoft YaHei", "微软雅黑", "Helvetica Neue", Helvetica,
        "PingFang SC", "Hiragino Sans GB", Arial, sans-serif;
        color: #333;
        font-weight: bold;
    }
</style>


