/**
* 法规库-裁量基准组件
* @module 案件
* @desc 列表
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <div class="wrop-search">
            <lj-search-form :onAdd="onAdd" :searchObj="searchObj" @searchList="searchList"/>
        </div>
        <!-- 表格 -->
        <el-table
                v-loading="loading"
                :data="list"
                :header-cell-style="{background:'#e9eff8'}"
                :row-style="{cursor:'pointer'}"
                @row-click="seeFunction"
                border
                size="mini"
        >
            <el-table-column align="center" label="基准名称" prop="dsName"/>
            <el-table-column
                    :show-overflow-tooltip="true"
                    align="center"
                    label="依据条例"
                    prop="accordingRegulations"
            />
            <el-table-column
                    :show-overflow-tooltip="true"
                    align="center"
                    label="适用情形"
                    prop="suitsituation"
            />
            <el-table-column
                    :show-overflow-tooltip="true"
                    align="center"
                    label="罚款幅度"
                    prop="pubnishextend"
            />
            <el-table-column align="center" label="是否生效" prop="isEffective" width="100">
                <template slot-scope="scope">{{scope.row.isEffective|isEffective}}</template>
            </el-table-column>
            <el-table-column align="left" label="操作" width="180">
                <template slot-scope="scope">
                    <el-tooltip :open-delay="500" content="查看该事项历史版本" placement="top">
                        <el-button
                                @click.stop="watchLog(scope.$index, scope.row)"
                                plain
                                size="mini"
                                type="primary"
                        >历史版本
                        </el-button>
                    </el-tooltip>
                    <el-tooltip :open-delay="500" content="对当前项生成新版本" placement="top">
                        <el-button
                                @click.stop="reviseFunction(scope.$index, scope.row)"
                                plain
                                size="mini"
                                type="warning"
                        >修订
                        </el-button>
                    </el-tooltip>
                    <el-tooltip :open-delay="500" content="修改当前项" placement="top">
                        <el-button
                                @click.stop="editFunction(scope.$index, scope.row)"
                                plain
                                size="mini"
                                type="warning"
                                v-if="scope.row.isEffective !== 1"
                        >编辑
                        </el-button>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <lj-pagination
                :pageNum.sync="form.pageNum"
                :pageSize.sync="form.pageSize"
                :total="total"
                @getData="getList"
                class="page-css"
        />

        <!-- 编辑&添加弹框 -->
        <change-edit
                :isRevise="editObj.isRevise"
                :pmId="pmId"
                :tid="tid"
                :show.sync="editObj.show"
                :title="editObj.title"
                :type="editObj.editId"
                @getList="getList"
        />
        <!-- 编辑&添加弹框 -->
        <log-list-dialog :id="logObj.id" :show.sync="logObj.show" @getList="getList"/>
        <!-- 查看 -->
        <see-content :editId="seeObj.editId" :show.sync="seeObj.show" :title="seeObj.title"></see-content>
    </div>
</template>

<script>
    import LjPagination from "../../../../components/LjPagination";
    import LjSearchForm from "../../../../components/LjSearchForm";
    import {isEffective} from "../../../../until/wordBook";
    import logListDialog from "./logList";
    import changeEdit from "./editadd";
    import {apiDsDelete, apiDsSearch} from "../../../../api/case";
    import seeContent from "./seeContent";

    export default {
        name: "discretionPage",
        data() {
            return {
                loading:true,
                //弹出框数据
                editObj: {
                    show: false,
                    title: "",
                    editId: null
                },
                logObj: {
                    show: false,
                    id: ""
                },
                seeObj: {
                    show: false,
                    title: "",
                    editId: null
                },
                //搜索组件数据
                searchObj: null,
                form: {
                    pageSize: 20,
                    isEffective: 1,
                    pageNum: 1
                },
                total: 0,
                list: [],
                see: false
            };
        },
        components: {
            changeEdit,
            LjPagination,
            LjSearchForm,
            logListDialog,
            seeContent
        },
        props: ["pmId","tid"],
        methods: {
            //查询+搜索
            getList() {
                this.form.pmId = this.pmId;
                apiDsSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    this.loading = false;
                });
            },
            //查看
            seeFunction(row) {
                this.seeObj.show = true;
                this.seeObj.title = "查看全部";
                this.seeObj.editId = row.cdsId;
            },
            //新增
            onAdd() {
                this.editObj.editId = null;
                this.editObj.title = "新增裁量基准";
                this.editObj.show = true;
            },
            watchLog(index, data) {
                this.logObj.id = data.dsId;
                this.logObj.show = true;
            },
            reviseFunction(index, row) {
                this.editObj.editId = row.cdsId;
                this.editObj.title = "修订";
                this.editObj.show = true;
                this.editObj.isRevise = true;
            },

            //编辑
            editFunction(index, row) {
                this.editObj.editId = row.cdsId;
                this.editObj.title = "修改";
                this.editObj.show = true;
            },
            //删除按钮事件
            deleteFunction(index, data) {
                this.$confirm("此操作将永久删除该领域, 是否继续?", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                }).then(() => {
                    apiDsDelete(data.cdsId).then(res => {
                        this.getList();
                        this.$message({
                            type: "success",
                            message: "删除成功!"
                        });
                    });
                });
            },
            //搜索
            searchList(data) {
                //data就是接收的this.form数据
                this.loading = true;
                data.pageNum = 1;
                data.isEffective=1;
                data.pmId = this.pmId;
                apiDsSearch(data).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    this.loading = false;
                });
            }
        },
        filters: {
            //时间戳
            formatDate(value) {
                return formatDate(value, "yyyy-MM-dd hh:mm:ss");
            },
            // 是否生效
            isEffective(val) {
                return isEffective[val];
            }
        },
        watch: {
            pmId() {
                this.getList();
            }
        },
        created() {
            this.getList();
        }
    };
</script>

<style scoped>
    .wrop-search {
        margin-bottom: 10px;
    }

    .page-css {
        margin-top: 10px;
    }
</style>
